import {mapState,mapMutations} from 'vuex';

export default {
	components: {
		
	},
	data() {
		return {
			bankList:[], //银行卡列表
			start_slide_x: 0,
			btnWidth: 0,
			startX: 0,
			LastX: 0,
			startTime: 0,
			mode:''
			
		}
	},
	computed: {
		...mapState(['token']),
		Screen_width() {
			return uni.getSystemInfoSync().windowWidth;
		}
	},
	onShow(){
		this.getCardList();
	},
	onLoad(e) {
		if(e['mode']) {
			console.log(e);
			var mode = e['mode'];
			this.mode = mode;
		}
	},
	onUnload(){
		this.$eventHub.$off('fire');
	},
	methods: {
		getCardList(){
			uni.request({
				url: this.websiteUrl + 'user/bank_list',
				method: 'POST',
				data:{},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("获取银行卡列表成功："+JSON.stringify(res.data));
						let data = res.data;
						let bankList = res.data;
						bankList.forEach((item)=>{
							item.slide_x = 0;
							var account = item.bankNo;
							var bankNo = account.substr(account.length-4,4);
							item.bankNo = bankNo;
							return item;
						})
						this.bankList = bankList;
						console.log('修改数据结构之后的bankList',bankList)
						
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 删除
		deleteCard(cardId) {
			var that = this;
		    uni.showModal({
		    	title: '温馨提示',
		    	content: '确认删除该条银行卡信息？',
		    	success: function (res) {
		    		if (res.confirm) {
		    			console.log('用户点击确定');
		    			uni.request({
		    				url: that.websiteUrl + 'user/bank_del',
		    				method: 'POST',
		    				data:{
		    					id:cardId
		    				},
		    				dataType:'json',//服务器返回json格式数据
		    				header:{'Content-Type':'application/json','Token':that.token},
		    				success: res => {
		    					var res = res.data;
		    					if(res.code == 200){
		    						console.log("删除银行卡成功："+JSON.stringify(res));
		    						var that2 = that;
		    						uni.showToast({
		    							icon: 'success',
		    							title: '删除银行卡成功',
		    							duration:2000,
		    							success:function(){
		    								that2.getCardList();
		    							}
		    						});
		    					}
		    				},
		    				fail: () => {},
		    				complete: () => {}
		    			});
		    		} else if (res.cancel) {
		    			console.log('用户点击取消');
		    			that.getCardList();
		    		}
		    	}
		    });
		},
		// 编辑银行账号
		toCardEdit(cardId) {
			console.log(cardId);
			if(cardId){
				uni.navigateTo({
					url:'/pages/mycenter/card/card-edit/card-edit?cardId='+cardId
				})
			}else {
				uni.navigateTo({
					url:'/pages/mycenter/card/card-edit/card-edit'
				})
			}
		   
		},
			// 滑动开始
		touchStart(e, index) {
		    //记录手指放上去的时间
		    this.startTime = e.timeStamp;
		    //记录滑块的初始位置
		    this.start_slide_x = this.bankList[index].slide_x;
		    // 按钮宽度
		    uni.createSelectorQuery()
		        .selectAll('.group-btn')
		        .boundingClientRect()
		        .exec(res => {
		            if (res[0] != null) {
		                this.btnWidth = res[0][index].width * -1;
		            }
		        });
		    // 记录上一次开始时手指所处位置
		    this.startX = e.touches[0].pageX;
		    // 记录上一次手指位置
		    this.lastX = this.startX;
		    //初始化非当前滑动消息列的位置
		    this.bankList.forEach((item, eq) => {
		        if (eq !== index) {
		            item.slide_x = 0;
		        }
		    });
		},
		// 滑动中
		touchMove(e, index) {
		    const endX = e.touches[0].pageX;
		    const distance = endX - this.lastX;
		    // 预测滑块所处位置
		    const duang = this.bankList[index].slide_x + distance;
		    // 如果在可行区域内
		    if (duang <= 0 && duang >= this.btnWidth) {
		        this.bankList[index].slide_x = duang;
		    }
		    // 此处手指所处位置将成为下次手指移动时的上一次位置
		    this.lastX = endX;
		},
		// 滑动结束
		touchEnd(e, index) {
		    let distance = 10;
		    const endTime = e.timeStamp;
		    const x_end_distance = this.startX - this.lastX;
		    if (Math.abs(endTime - this.startTime) > 200) {
		        distance = this.btnWidth / -2;
		    }
		    // 判断手指最终位置与手指开始位置的位置差距
		    if (x_end_distance > distance) {
		        this.bankList[index].slide_x = this.btnWidth;
		    } else if (x_end_distance < distance * -1) {
		        this.bankList[index].slide_x = 0;
		    } else {
		        this.bankList[index].slide_x = this.start_slide_x;
		    }
		},
		// 点击回复原状
		recover(index) {
		    this.bankList[index].slide_x = 0;
		}
		
	}
}
